// 下载文件，并发送给浏览器下载
import { EVT } from './EVT';
import { titleBar } from './TitleBar';
import { fileName } from './FileName';
import { progressBar } from './ProgressBar';
class Download {
    constructor(progressBarIndex, data) {
        this.fileName = '';
        this.stoped = false;
        this.progressBarIndex = progressBarIndex;
        this.arg = data;
        this.download(data);
        this.listenEvents();
    }
    listenEvents() {
        ;
        [EVT.events.downloadStop, EVT.events.downloadPause].forEach((event) => {
            window.addEventListener(event, () => {
                this.stoped = true;
            });
        });
        window.addEventListener(EVT.events.downloadSucccess, (event) => {
            const donwloadSuccessData = event.detail.data;
            if (donwloadSuccessData.url === this.arg.data.url) {
                this.setProgressBar(1024, 1024);
            }
        });
    }
    // 设置进度条信息
    setProgressBar(loaded, total) {
        progressBar.setProgress(this.progressBarIndex, {
            name: this.fileName,
            loaded: loaded,
            total: total,
        });
    }
    // 下载文件
    download(arg) {
        titleBar.change('↓');
        // 获取文件名
        this.fileName = fileName.getFileName(arg.data);
        // 重设当前下载栏的信息
        this.setProgressBar(0, 0);
        // 向浏览器发送下载任务
        this.browserDownload(arg.data.url, this.fileName, arg.id, arg.taskBatch);
    }
    // 向浏览器发送下载任务
    browserDownload(url, fileName, id, taskBatch) {
        // 如果任务已停止，不会向浏览器发送下载任务
        // if (this.stoped) {
        //   // 释放 bloburl
        //   url.startsWith('blob') && URL.revokeObjectURL(url)
        //   return
        // }
        const sendData = {
            msg: 'send_download',
            fileUrl: url,
            fileName: fileName,
            id,
            taskBatch,
        };
        chrome.runtime.sendMessage(sendData);
    }
}
export { Download };
